/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.qodef-woocommerce-page .qodef-content {

    @include laptop-landscape-mac() {

        .variations {

            tr {
                width: 40%;
            }
        }
    }

    @include ipad-landscape() {

        .variations {

            tr {
                width: 28%;
            }
        }
    }

    @include ipad-portrait() {

        .variations {

            tr {
                width: 33%;
            }
        }
    }

    @include phone-landscape() {

        .variations {

            tr {
                width: 100%;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */